'use strict';
import {Event} from "./event";

// Interface for the ContextMenuInitialize event
export interface ContextMenuInitializeEvent extends Event {
    // The id of the context menu to display.
    Id: string;

    // The label to give to the context menu for the screen reader to read.
    AriaLabel: string;

    // The InnerHTML of the root and submenus.
    ContextMenus: string;
}